.App {
    text-align: center;
  }
  
  .App-main {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
  }
  
  .container{
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    background-color: rgba(0,0,0, 0.8);
  }
  
  .shell{
    background-color: #282c34;
    padding: 4px 25px;
    color: #eee;
    border-radius: 8px;
    font-weight: 100;
  }
  
  .tagline {
    font-size: 1.5rem;
    margin: 1.5rem 0;
    font-weight: 200;
    margin-left: 1.5em;
    margin-right: 1.5em;
  }
  
  .intro {
    font-family: 'Reem Kufi', sans-serif;
    font-size: 2.75em;
    font-weight: 600;
    margin-left: 0.5em;
    margin-right: 0.5em;
  }
  
  .change-mode {
    position: absolute;
    top: 25px;
    right: 25px;
  }
  
  .icons-social i {
    padding: 10px;
    font-size: 2em;
  }
  
  @media (min-width: 576px) {
    html {
      font-size: 14px;
    }
  }
  
  @media (min-width: 768px) {
    html {
      font-size: 16px;
    }
  }
  
  @media (min-width: 992px) {
    html {
      font-size: 18px;
    }
  }
  
  @media (min-width: 1200px) {
    html {
      font-size: 20px;
    }
  }
  
  /* Daylight CSS */
  
  .daylight .App-main {
    color: white;
  }
  
  .daylight .change-mode:before {
    color: yellow;
    font-family: 'Font Awesome 5 Free';
    font-weight: 400;
    content: '\f185';
  }
  
  .daylight .icons-social a {
    color: #fafafa;
  }
  
  .daylight .icons-social a svg path {
    fill: #fafafa;
  }
  
  /* Nightlight CSS */
  
  .nightlight .App-main {
    background-color: #fafafa;
    color: #000;
  }
  
  .nightlight .change-mode:before {
